import React from 'react'
import { SidebarContainer, Icon, CloseIcon, SidebarMenu, SideBtnWrap, SidebarRoute, SidebarLink } from './SidebarElements';

export const Sidebar = ({isOpen,toggle}) => {
    return (
        <SidebarContainer isOpen={isOpen} onClick={toggle}>
            <Icon>
                <CloseIcon />
            </Icon>
            <SidebarMenu>
                <SidebarLink to="/">
                    Pizzas
                </SidebarLink>
                <SidebarLink to="/">
                    Desserts
                </SidebarLink>
                <SidebarLink to="/">
                    Full Menu
                </SidebarLink>
            </SidebarMenu>
            <SideBtnWrap>
                <SidebarRoute to="/" >
                    Order Menu
                </SidebarRoute>
            </SideBtnWrap>
        </SidebarContainer>
    );
};

export default Sidebar;